/* Main App */
export default function App() {
  const [screen, setScreen] = useState("splash");
  const [userType, setUserType] = useState(null);

  const go = (page) => setScreen(page);

  const logout = async () => {
    await signOut(auth);
    setScreen("role");
    setUserType(null);
  };

  const onStudentLoginSuccess = () => {
    setUserType("student");
    setScreen("studentDash");
  };
  const onCounselorLoginSuccess = () => {
    setUserType("counselor");
    setScreen("counselorDash");
  };

  return (
    <div style={styles.page}>
      {screen === "splash" && <Splash onContinue={() => setScreen("role")} />}
      {screen === "role" && <RoleSelection go={go} />}
      {screen === "studentLogin" && (
        <StudentLogin
          onBack={() => setScreen("role")}
          onLoginSuccess={onStudentLoginSuccess}
          onRegisterClick={() => setScreen("studentRegister")}
        />
      )}
      {screen === "studentRegister" && <StudentRegister onBack={() => setScreen("studentLogin")} />}
      {screen === "studentDash" && <StudentDashboard onLogout={logout} />}
      {screen === "counselorLogin" && (
        <CounselorLogin
          onBack={() => setScreen("role")}
          onLoginSuccess={onCounselorLoginSuccess}
          onRegisterClick={() => setScreen("counselorRegister")}
        />
      )}
      {screen === "counselorRegister" && <CounselorRegister onBack={() => setScreen("counselorLogin")} />}
      {screen === "counselorDash" && <CounselorDashboard onLogout={logout} />}
    </div>
  );
}

